/*************************** Announcement Section Styles ****************************/


@mixin announcement-panel ($media-min, $media-max, $padding-min, $padding-max) {

	

	$d: ($padding-max - $padding-min) / 1px;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$padding: $i + $padding-min;

			@include rem(padding-top, $padding);

		}
	}
}


@mixin announcement-img-desktop ($media-min, $media-max, $margin-min, $margin-max) {

	

	$d: ($margin-max - $margin-min) / 1px;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$margin: $i + $margin-min;

			@include rem(margin-top, $margin);

		}
	}
}



