@mixin announcement-p ($media-min, $media-max, $font-size-min, $font-size-max) {

	$d: ($font-size-max - $font-size-min) / 1px;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$font-size: $i + $font-size-min;

			@include rem(font-size, $font-size);

			@include rem(line-height, round($font-size * 1.67));
//			@include rem(margin-top, round($font-size * 1.31));

			&:first-child {
//				@include rem(margin-top, round($font-size * 3.33));
				@include rem(margin-top, $font-size);
				@include rem(margin-bottom, round($font-size * 3.33));
