@mixin announcement-panel-margin ($media-min, $media-max, $margin-min, $margin-max) {


	


	$d: ($margin-max - $margin-min) / 1%;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$margin: $i + $margin-min;

			margin-left: $margin;
			margin-right: $margin;

//			@include rem(padding-top, $padding);

//			@include rem(line-height, round($font-size * 1.25));
//			@include rem(margin-bottom, round($font-size * 0.75));

		}
	}
}









@mixin announcement-col ($media-min, $media-max, $padding-min, $padding-max) {

	

	$d: ($padding-max - $padding-min) / 1px;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$padding: $i + $padding-min;

			@include rem(padding-top, $padding);
			@include rem(padding-bottom, $padding * 4);

		}
	}
}












